.container {
  text-align: center;
  height: 100vh;
  width: 100%;
}
.visible {
  visibility: visible !important;
}
.invisible {
  visibility: hidden !important;
}
.flow_canvas {
  text-align: center;
  height: 100%;
  width: 100%;
}
.modal {
  width: 80%;
  position: absolute;
  animation-name: fadeIn;
  top: 50%;
  animation-duration: .3s;
  left: 50%;
  transform: translate(-50%, -80%);
  z-index: 2;
  background-color: white;
  border: 1.5px solid #ebebeb;
  border-radius: 8px;
  padding: 20px 30px;
  box-shadow:
    0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072),
    0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12)
}
@keyframes fadeIn {
  from { top: -10%; }
  to { top: 50%; }
}
.react-flow {
  z-index: 1;
}
.modal_what {
  text-align: left;
  margin: 10px 0 30px 0;
  border-bottom: 1px solid #ebebeb;
}
.modal_what:hover {
  border-bottom: 1px solid #A5A6F6;
}
.modal_when {
  text-align: left;
  margin: 10px 0 10px 0;
  border-bottom: 1px solid #ebebeb;
}
.modal_when:hover {
  border-bottom: 1px solid #A5A6F6;
}
.modal_title {
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 3px;
}
.modal_detail {
  height: 130px;
  margin-top: 30px;
  text-align: left;
}
.modal_content {
  font-size: 18px;
  padding: 0 0 10px 5px;
  margin-top: 5px;
  width: 100%;
  border: none;
  outline: none;
}
.modal_detail > .modal_content {
  border: 1px solid gray;
}
.btn_container {
  position: absolute;
  bottom: -25px;
  right: 20px;
  display: flex;
  align-items: center;
}
.btn_dissmiss {
  cursor: pointer;
  padding: 15px 20px;
  background: white;
  border-radius: 30px;
  border: 1px solid #ebebeb;
  box-shadow:
  0 2.8px 2.2px rgba(0, 0, 0, 0.034),
  0 6.7px 5.3px rgba(0, 0, 0, 0.048),
  0 12.5px 10px rgba(0, 0, 0, 0.06),
  0 22.3px 17.9px rgba(0, 0, 0, 0.072),
  0 41.8px 33.4px rgba(0, 0, 0, 0.086),
  0 100px 80px rgba(0, 0, 0, 0.12)
}
.btn_delete {
  cursor: pointer;
  margin-left: 30px;
  background: white;
  padding: 10px;
  border-radius: 100%;
  border: 1px solid #ebebeb;
  box-shadow:
  0 2.8px 2.2px rgba(0, 0, 0, 0.034),
  0 6.7px 5.3px rgba(0, 0, 0, 0.048),
  0 12.5px 10px rgba(0, 0, 0, 0.06),
  0 22.3px 17.9px rgba(0, 0, 0, 0.072),
  0 41.8px 33.4px rgba(0, 0, 0, 0.086),
  0 100px 80px rgba(0, 0, 0, 0.12)
}
.btn_delete_text {
  cursor: pointer;
  padding: 15px 20px;
  background: white;
  border-radius: 30px;
  border: 1px solid #ebebeb;
  color: #fb7474;
  font-weight: bold;
  margin-left: 10px;
  box-shadow:
  0 2.8px 2.2px rgba(0, 0, 0, 0.034),
  0 6.7px 5.3px rgba(0, 0, 0, 0.048),
  0 12.5px 10px rgba(0, 0, 0, 0.06),
  0 22.3px 17.9px rgba(0, 0, 0, 0.072),
  0 41.8px 33.4px rgba(0, 0, 0, 0.086),
  0 100px 80px rgba(0, 0, 0, 0.12)
}
.btn_save {
  cursor: pointer;
  margin-left: 30px;
  background: white;
  padding: 10px;
  border-radius: 100%;
  border: 1px solid #ebebeb;
  box-shadow:
  0 2.8px 2.2px rgba(0, 0, 0, 0.034),
  0 6.7px 5.3px rgba(0, 0, 0, 0.048),
  0 12.5px 10px rgba(0, 0, 0, 0.06),
  0 22.3px 17.9px rgba(0, 0, 0, 0.072),
  0 41.8px 33.4px rgba(0, 0, 0, 0.086),
  0 100px 80px rgba(0, 0, 0, 0.12)
}
.modal_background {
  background-color: gray;
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  z-index: 1;
  opacity: .6;
}
/* overwrite react-flow style */

.react-flow__node-default {
  border: none !important;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}
.react-flow__handle {
  visibility: hidden !important;
}
.react-flow__edge-path {
  stroke: #A5A6F6;
}
.header_container {
  position: absolute;
  height: 50px;
  background-color: #e1e1e1;
  width: 100vw;
  text-align: center;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 5;
}
.header_container a {
  color: #4a4a4a;
  text-decoration: unset;
  font-size: 18px;
}
@media only screen and (min-width: 1300px) {
  .btn_container {
    right: 50%;
    transform: translateX(50%);
  }
}